* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Open Sans Regular";
  color: #515151;
  overflow-x: hidden; }

@font-face {
  font-family: "Open Sans Light";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Open Sans Light.woff2") format("woff2"), url("../fonts/Open Sans Light.woff") format("woff"); }

@font-face {
  font-family: "Open Sans Regular";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Open Sans Regular.woff2") format("woff2"), url("../fonts/Open Sans Regular.woff") format("woff"); }

@font-face {
  font-family: "Open Sans Semibold";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Open Sans Semibold.woff2") format("woff2"), url("../fonts/Open Sans Semibold.woff") format("woff"); }

h2,
h3,
p {
  margin: 0; }

ul,
img {
  margin: 0;
  padding: 0; }

/*header*/
.head {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding-bottom: 23px; }

.head__wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 21px 140px 0 135px; }

.head__nav {
  width: 100%; }

.head__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style-type: none; }

.head__item:not(.head__item--logo) {
  margin-top: 13px; }

.head__item--logo {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1; }

.head__link {
  margin-right: 28px;
  font-size: 13.2px;
  font-family: "Open Sans semibold";
  text-transform: uppercase;
  text-decoration: none;
  color: #666666; }

.head__btn {
  margin: 4px 0 0 46px;
  padding: 0;
  min-width: 100px;
  height: 37px;
  font-size: 13.2px;
  font-family: "Open Sans semibold";
  text-transform: uppercase;
  background-color: #bcda5c;
  color: #ffffff;
  border-radius: 20px;
  border: none;
  outline: none;
  cursor: pointer; }

/*main*/
.banner__wrap {
  max-width: 1300px;
  height: 570px;
  margin: 0 auto;
  padding-top: 150px;
  text-align: center;
  background: url("../img/banner_bg.png") no-repeat, #c8cfca;
  background-blend-mode: multiply; }

.banner__title {
  margin: 0 200px;
  font-size: 49.2px;
  line-height: 77px;
  color: #ffffff; }

.banner__span {
  font-size: 35.2px;
  font-family: "Open Sans Light"; }

.banner__link {
  width: 255px;
  margin: 20px auto 0;
  display: block;
  font-size: 19.2px;
  line-height: 77px;
  text-decoration: none;
  color: #ffffff;
  background-color: #99c50a;
  border-radius: 15px; }

.promo {
  background: url("../img/icon_promo-bg.svg") no-repeat;
  background-size: 58px;
  background-position: 52.5% 99px;
  border-top: 1px solid rgba(255, 255, 255, 0); }

.promo__wrap {
  max-width: 1300px;
  margin: 0 auto;
  text-align: center; }

.promo__title {
  margin: 194px 380px 50px;
  font-size: 31.2px;
  line-height: 50px;
  font-family: "Open Sans Semibold";
  text-transform: uppercase; }

.promo__paragraph {
  margin: 0 180px 38px;
  font-size: 19.2px;
  line-height: 31.2px;
  font-family: "Open Sans Regular"; }

.advatages__wrap {
  max-width: 1300px;
  margin: 140px auto 0; }

.advantages__list {
  margin: 0 160px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  list-style-type: none;
  border-bottom: 1px solid #5f5f5f; }

.advantages__btn {
  position: relative;
  margin: 0;
  padding: 15px 0 33px 70px;
  font-size: 33.2px;
  font-family: "Open Sans Light";
  background-color: #ffffff;
  border: none;
  outline: none;
  cursor: pointer; }
  .advantages__btn::before {
    content: "";
    position: absolute;
    top: 14px;
    left: 7px;
    width: 43px;
    height: 43px;
    border-radius: 50%;
    background-color: #5f5f5f; }

.advantages__btn--active {
  border-bottom: 1px solid #add136; }
  .advantages__btn--active::before {
    background-color: #add136; }

.advantages__btn--smp {
  width: 200px;
  padding-left: 48px; }

.advantages__btn--trs {
  width: 270px; }

.advantages__btn--clb {
  width: 290px; }

.advantages__span {
  position: absolute;
  top: 21px;
  left: 23px;
  font-size: 20.19px;
  font-family: "Open Sans Regular";
  color: #ffffff; }

.advantages__text {
  position: relative;
  width: 700px;
  margin: 57px 0 0 464px; }
  .advantages__text::before {
    content: "";
    position: absolute;
    top: 30px;
    left: -216px;
    width: 113px;
    height: 102px;
    background: url("../img/icon-glasses.svg") no-repeat; }

.advantages__head {
  font-size: 25.2px;
  font-family: "Open Sans Regular";
  color: #add136; }

.advantages__paragraph {
  margin: 25px 0 0 2px;
  font-size: 17.2px;
  line-height: 30.2px;
  font-family: "Open Sans Regular"; }
